body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont,"Montserrat", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: "Montserrat", "Helvetica", "Arial", "sans-serif" 
}

.timelines-chart {

  text-align: center;

  /* Cancel selection interaction */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;

  .axises {
    line, path {
      stroke: #808080;
    }

    .x-axis {
      font: 12px sans-serif;
    }

    .x-grid line {
      stroke: #D3D3D3;
    }

    .y-axis, .grp-axis {
      line, path {
        stroke: none;
      }
      text {
        fill: #2F4F4F;
      }
    }
  }

  line.x-axis-date-marker {
    stroke-width: 1;
    stroke: #293cb7;
    fill: "none";
  }

  .series-group {
    fill-opacity: 0.6;
    stroke: #808080;
    stroke-opacity: 0.2;
  }

  .series-segment {
    stroke: none;
  }

  .series-group, .series-segment {
    cursor: crosshair;
  }

  .legend {
    font-family: Sans-Serif;

    .legendText {
      fill: #666;
    }
  }

  .reset-zoom-btn {
    font-family: sans-serif;
    fill: blue;
    opacity: .6;
    cursor: pointer;
  }
}

.brusher {
  .grid-background {
    fill: lightgrey;
  }

  .axis path {
    display: none;
  }

  .tick text {
    text-anchor: middle;
  }

  .grid {
    line, path {
      stroke: #fff;
    }
  }
}

.chart-zoom-selection, .brusher .brush .selection {
  stroke: blue;
  stroke-opacity: 0.6;
  fill: blue;
  fill-opacity: 0.3;
  shape-rendering: crispEdges;
}

.chart-tooltip {
  color: #eee;
  background: rgba(0,0,140,0.85);
  padding: 5px;
  border-radius: 3px;
  font: 11px sans-serif;
  z-index: 4000;

  &.group-tooltip {
    font-size: 14px;
  }

  &.line-tooltip {
    font-size: 13px;
  }

  &.group-tooltip, &.line-tooltip {
    font-weight: bold;
  }

  &.segment-tooltip {
     text-align: center;
  }
}
